body{
    background-color:  #5d8fc9;
}


.grid-template{
    display: grid;
    grid-template-columns: repeat(4, 25%);
    border:0;
    box-sizing: border-box;
    padding: 1% 0 0 1%;
    gap: .5em;
}

/*table*/
table { 
    width: 100%; 
    border-collapse: collapse; 
  }
  /* Zebra striping */
  tr:nth-of-type(odd) { 
    background: #eee; 
  }
  th { 
    background: #333; 
    color: white; 
    font-weight: bold; 
  }
  td, th { 
    padding: 6px; 
    border: 1px solid black; 
    text-align: left; 
  }


/*forms*/
.input__field{
  width:100%;
}

.form-create{
    width:40%;
    background-color:  #5d8fc9;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
    margin-top: 1.5em;
    border-radius: .5em;
    box-shadow: 15px 15px 50px rgba(0, 0, 0, 0.5);
}

.form-create > input{
    width:80%;
    height:2.2em;
    border:1px solid gray;
    border-radius:5px;
    border-color: #ebf0fc;
    color: black;
    margin: 0 auto;
    margin-bottom: .5em;
}


#login-card{
    width:350px;
    border-radius: 25px;
    margin:150px auto;
  
}

#email{
    border-radius:30px;
    background-color: #ebf0fc;
    border-color: #ebf0fc;
    color: #9da3b0;
}

#button{
    border-radius:30px;

}

#btn{
   position: absolute; 
   bottom: -35px; 
   padding: 5px;
   margin: 0px 55px;
   align-items: center;
   border-radius: 5px
}


/*pagination*/
.pagination {
    box-sizing: border-box;
    width: 40%;
    height:4em;
    display: flex;
    justify-content: space-between;
    gap:.5em;
    list-style: none;
    cursor: pointer;
    margin: 0 auto;
    margin-top: .5em;
    padding-top: .9em;
    border:0;
  }
  
  .pagination a {
    padding: 10px;
    border-radius: 5px;
    border: 1px solid black;
    color: black;
  }
  
  .pagination__link {
    font-weight: bold;
  }
  
  .pagination__link--active a {
    color: #fff;
    background: #6c7ac9;
  }
  
  .pagination__link--disabled a {
    color: rgb(198, 197, 202);
    border: 1px solid rgb(198, 197, 202);
  }
  
/*menu*/

.nav {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    width: 100%;
    height: 5em;
    background-color: #fff;
    box-shadow: 0 1rem 1rem -1rem rgba(55, 56, 58, 0.2);
    z-index: 1;
  }
  
  header{
      margin-bottom: 5em;
  }
  nav > ul {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
  
  nav li {
    padding: 0px 0.8rem;
    list-style: none;
  }

/*media queries
*/
@media screen and (max-width: 760px),
(min-device-width: 768px) and (max-device-width: 1024px)  {

	/* Force table to not be like tables anymore */
	table, thead, tbody, th, td, tr { 
		display: block; 
	}
	
	/* Hide table headers (but not display: none;, for accessibility) */
	thead tr { 
		position: absolute;
		top: -9999px;
		left: -9999px;
	}
	
	tr { border: 1px solid #ccc; }
	
	td { 
		/* Behave  like a "row" */
		border: none;
		border-bottom: 1px solid black; 
		position: relative;
		padding-left: 50%; 
	}
	
	td:before { 
		/* Now like a table header */
		position: absolute;
		/* Top/left values mimic padding */
		top: 6px;
		left: 6px;
		width: 45%; 
		padding-right: 10px; 
		white-space: nowrap;
    }
    
    td:nth-of-type(1):before { content: "Nombre"; }
	td:nth-of-type(2):before { content: "Usuario"; }
	td:nth-of-type(3):before { content: "Correo-e"; }
	td:nth-of-type(4):before { content: "Direccion"; }
    td:nth-of-type(5):before { content: "Opcion"; }
    
    .grid-template{
        grid-template-columns: repeat(1, 100%);
        padding-top: 1em;
    }

    .card{
        margin: 0 auto;
    }

    .pagination {
        width:80%;
    }

    .form-create{
        width: 90%;
    }
}